.button {
  width: 102px;
  height: 48px;

  font-size: 12px;
  text-align: center;
  line-height: 48px;
  text-transform: uppercase;
  color: white;
  border-color: transparent;

  cursor: pointer;

  transition: background-color $duration;

  &:hover {
    background-color: $hover-color;
  }
}
